/* App.css */
body {
  font-family: 'Arial', sans-serif;
  background-color: #e8f5e9;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.App {
  text-align: center;
}

.main-page h1, .main-page h2 {
  color: #388e3c;
}

.exercise-buttons {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 20px;
}

button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 15px;
  font-size: 18px;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
  width: 200px;
  margin: 0 auto;
}

button:hover {
  background-color: #388e3c;
}

.flashcards {
  text-align: center;
}

.card {
  width: 200px;
  height: 150px;
  border: 1px solid #000;
  margin: 20px auto;
  perspective: 1000px;
}

.card-inner {
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
  position: relative;
}

.card.flipped .card-inner {
  transform: rotateY(180deg);
}

.card-front, .card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  backface-visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-back {
  transform: rotateY(180deg);
}

.buttons {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}

.know-btn, .dont-know-btn {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  color: white;
  border: none;
}

.know-btn {
  background-color: green;
}

.dont-know-btn {
  background-color: red;
}

.exit-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
}

.confirmation-modal {
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}

.confirmation-modal button {
  margin: 10px;
  padding: 10px 20px;
  cursor: pointer;
}